@import url(https://fonts.googleapis.com/css2?family=Square+Peg&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

:root {
  --color-bg: #1f1f38;
  --color-bg-variant: #2c2c6c;
  --color-primary: #4db5ff;
  --color-primary-variant: rgba(77, 181, 255, 0.4);
  --color-white: #fff;
  --color-light: rgba(255, 255, 255, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  /* font-family: 'Square Peg', cursive; */
  background: #1f1f38;
  background: var(--color-bg) ;
  color: #fff;
  color: var(--color-white);
  line-height: 1.7;
  background-image: url(data:image/png;base64,);
  
}


/* ================= GENERAL STYLE ============== */
.container {
  width: 75%;
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

section > h2 {
  color: #4db5ff;
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

a {
  color: #4db5ff;
  color: var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

a:hover {
  color: #fff;
  color: var(--color-white);
}

.btn {
  width: -webkit-max-content;
  width: max-content;
  display: inline-block;
  color: #4db5ff;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid #4db5ff;
  border: 1px solid var(--color-primary);
  transition: all 400ms ease;
  transition: var(--transition);
}

.btn:hover {
  background: #fff;
  background: var(--color-white);
  color: #1f1f38;
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: #4db5ff;
  background: var(--color-primary);
  color: #1f1f38;
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

  
/* =========== MIAGE AND TIME & DATE ========== */

.heading{
  text-align: center;
  text-transform: uppercase;
  font-weight: bolder;
}

.t_d{
  color: blueviolet;
  text-align: center;
  text-transform: capitalize;
  font-weight: bold;
  text-shadow: 0px 2px 4px blue;
}

.images{
  display: flex;
  justify-content:center;
}

.images img{
  width: 200px;
  height: 320px;
  margin-right: 10px;
  margin-bottom: 20px;
}

/* =========== HEADER ========== */
header {
  height: 100vh;
  padding-top: 7rem;
  overflow: hidden;
}

.header_con {
  text-align: center;
  height: 100%;
  position: relative;
}


.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

.header_socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header_socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background: #4db5ff;
  background: var(--color-primary);
}

.me {
  background: linear-gradient(#4db5ff, transparent);
  background: linear-gradient(var(--color-primary), transparent);
  width: 22rem;
  height: 30rem;
  position: absolute;
  left: calc(50% - 11rem);
  margin-top: 4rem;
  border-radius: 12rem 12rem 0 0;
  overflow: hidden;
  padding: 5rem 1.5rem 1.5rem 1.5rem;
}

/* ============= ABOUT ================= */

.know_me {
  text-align: center;
  height: 100vh;
  padding-top: 30rem;

}
/* .p {
  text-align:left;
  position:relative;
  margin: 2rem 0 2.6rem;
  color: var(--color-light);
}  */


.about_con {
  display:inline;
  grid-template-columns: 35% 50%;
  grid-gap: 15%;
  gap: 15%;
  
}

.about_me {
  width: 22rem;
  height: 30rem;;
  aspect-ratio: 0.2/0.2;
  border-radius: 2rem;
  background: linear-gradient(
      45deg,
      transparent,
      #4db5ff,
      transparent
  );
  background: linear-gradient(
      45deg,
      transparent,
      var(--color-primary),
      transparent
  );
  display:hidden;
  /* place-items:center; */
  position:relative;
  left: calc(30% - 20rem);
  /* margin-top: 1rem; */

}

.about_me-image {
  border-radius: 5rem;
  width: 22rem;
  height: 30rem;
  overflow:hidden;
  -webkit-transform-origin: rotate(10deg);
          transform-origin: rotate(10deg);
}

.about_me-image:hover {
  -webkit-transform: rotate(10deg);
          transform: rotate(10deg);
}


.about_cards {
  display: grid;
  position:relative;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
}

.about_card {
  background: #2c2c6c;
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 3rem;
  text-align: center;
}

.about_card:hover {
  background: transparent;
  border-color: rgba(77, 181, 255, 0.4);
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about_icon {
  color: #4db5ff;
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}

.about_card h5 {
  font-size: 1rem;
}

.about_card small {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.6);
  color: var(--color-light);
}

.btn {
  background: #4db5ff;
  background: var(--color-primary);
  color: #1f1f38;
  color: var(--color-bg);
}

/* ======================== CONTACTS ========================== */

.cont {
  text-align: center;
  position:relative;
  overflow: hidden;
}
.contact_container {
  width: 58%;
  display: grid;
  grid-template-columns: 30% 58%;
  grid-gap: 12%;
  gap: 12%;
}

.contact_options {
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
.contact_option {
  background: #2c2c6c;
  background: var(--color-bg-variant);
  padding: 1.2rem;
  border-radius: 1.2rem;
  text-align: center;
  border: 1px solid transparent;
  transition: var(--transitions);
}

.contact_option:hover {
  background: transparent;
  border-color: rgba(77, 181, 255, 0.4);
  border-color: var(--color-primary-variant);
}

.contact_option-icons {
  font-size: 1.2rem;
  margin-bottom: 0.5rem;
}
.contact_option a {
  margin-top: 0.7rem;
  display: inline-block;
  font-size: 0.8rem;
}

/* ========= form ======== */

form {
  align-items: left;
  position: relative;
  display: flex;
  flex-direction: column;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}

input, textarea {
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.5rem;
  background: transparent;
  border: 2px solid rgba(77, 181, 255, 0.4);
  border: 2px solid var(--color-primary-variant);
  resize: none;
  color: #fff;
  color: var(--color-white);
  
}

/* ============= MEDIA QUERIES (MEDIUM DEVICES) ==========*/
 @media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }
  /* ===== about ===== */
   .about_con {
    grid-template-columns: 1fr;
    grid-gap: 0;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content p{
    margin: 1rem 0 1.5rem;
  } 
 }

/* ============= MEDIA QUERIES (SMALL DEVICES) ==========*/

 @media screen and (max-width: 600px) {
  .container {
    width: 90%;
    width: var(--container-width-sm);
  }
  section > h2 {
    margin-bottom: 2rem; 
  }
  header {
    height: 100vh;
  }
  .header_socials {
    display: none;
  }
 
  /* ==== about ===== */
   .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about_cards {
    grid-template-columns: 1fr 1fr;
    grid-gap: 1rem;
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
  } 
 }




